html{box-sizing:border-box}html{font-size:62.5%}*,*::before,*::after{padding:0;box-sizing:inherit}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-x:hidden;background-color:#fafafa}.App{text-align:center;display:flex;flex-direction:column;align-items:center;background-color:#fafafa;height:105vh}.main{margin-top:5vh}.header-icons{display:flex;align-items:center;justify-content:space-evenly}.header-icons div{margin-left:auto;width:12vw;display:flex;align-items:center;justify-content:space-evenly}.header-icons img{border-radius:50%;width:32px;height:32px}.icon{font-size:2.3rem;color:#282828;cursor:pointer}

.header{width:100vw;height:5.3rem;background-color:#fff;border-bottom:1px solid #dbdbdb;position:fixed;width:100%;z-index:3;display:flex;justify-content:center}.header-logo{width:36rem;display:flex;align-items:center;justify-content:flex-start}.header-icons{width:36rem}.header-icons div{width:23rem}.header-search{flex:0 1 auto;font-size:1.3rem;background-color:rgba(219,219,219,0.2);border-radius:3px;outline:0;padding:5px 10px;z-index:2;border:none}

.search{position:relative;color:#8e8e8e;font-size:13px;display:flex;align-items:center}.search span{transform:translateX(2rem);z-index:3}.search input{min-width:125px;width:20rem;height:28px;background:#fcfcfc;border:1px solid rgba(142,142,142,0.1);border-radius:3px}.search input{text-indent:1.5rem}

.profile{margin-top:5rem;width:93.5rem;height:31rem;display:flex}.profile-img{width:30%;margin-right:4%}.profile-img img{border-radius:50%}.profile-aside{width:66%}.profile-aside-buttons{display:flex;align-items:center}.profile-aside-buttons h2{margin:0;font-size:28px;font-weight:normal;color:#262626;margin-right:2rem}.profile-aside-buttons button{font-size:14px;-webkit-appearance:none;-moz-appearance:none;appearance:none;background:0 0;border:0;cursor:pointer;font-weight:600;padding:5px 9px;text-align:center;text-transform:inherit;text-overflow:ellipsis;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:auto;border-radius:4px;color:#fff;border:1px solid transparent;background-color:#0095f6}.profile-aside-buttons button:hover{opacity:0.8}.profile-aside-stats{margin-top:3rem}.profile-aside-stats ul{font-size:1.6rem;width:17vw;display:flex;justify-content:space-between}.profile-aside-stats ul li{list-style:none;font-weight:bold}.profile-aside-stats ul span{margin-left:0.3rem;font-weight:normal}.profile-aside-description{line-height:1.7;text-align:left;list-style-type:none;font-size:1.6rem}.profile-aside-description h3{font-size:1.6rem;padding:0;margin:0;font-weight:500}.profile-aside-description li span{padding-right:5px}.profile-aside-description li a{color:#00376b;font-weight:600;text-decoration:none}.profile-aside-followed{margin-top:10px;text-align:left;color:#606060;font-size:1.2rem}

.card-list{display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center;grid-gap:2.5rem;gap:2.5rem}

.card{position:relative;border-radius:5px;cursor:pointer;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:translateZ(0);transition:transform 0.25s ease-out;font-weight:normal;width:29.3rem;height:29.3rem}.card img{width:100%;height:100%;object-fit:cover}.card img:hover{-webkit-filter:brightness(50%);filter:brightness(50%);opacity:0.95}.card div{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);font-size:1.8rem;color:#f0eaea;z-index:5;width:65%;display:flex;align-items:center;pointer-events:none;justify-content:space-around}.card div li{width:auto;display:flex;align-items:center}.card div li span{margin-left:0.6rem}

.navigation{display:flex;flex-direction:column;align-items:center;border-top:1px solid #dbdbdb;color:#8e8e8e;font-size:12px;font-weight:600;justify-content:center;letter-spacing:1px;text-align:center;list-style-type:none;text-transform:uppercase;width:93.5rem;height:5.3rem;overflow:hidden}.navigation ul{margin:0;height:5.3rem;display:flex;width:20vw;justify-content:space-evenly;align-items:center}.navigation li{width:4.2vw;cursor:pointer;display:flex}.navigation li a{margin-left:0.8rem;color:#8e8e8e;text-decoration:none}.navigation .active{color:#181818}

.loading{font-size:5rem;color:gray;z-index:9999;height:10vh;overflow:hidden;display:flex;align-items:center;justify-content:center}

